import { mapState } from 'vuex'
    export default {
        name: "goodshow",
        data () {
            return {
                showbox:false,
                goodsnum:1,
                goods:[],
                names:"",
                save_price:"",
                style_img:"",
                style_price:"",
                style_stock:""
            }
        },
        computed: {
            ...mapState({
              goods_id: state => state.goods_id
            }),
            // 总金额
            moneynum:function(){
                return this.goods.member_price * this.goodsnum
            }
        },
        mounted:function(){
            this.$axios.get('/goods/detail',{params:{goods_id:this.goods_id}}).then(res=>{
                console.log(res.data.data);
                this.goods=res.data.data;
                this.save_price=this.goods.market_price-this.goods.member_price;


                console.log(this.goods.spec_group);

                // this.style_price=this.goods.info[0].member_price;
            })
        },
        methods:{
            goodshow(){
                this.showbox=!this.showbox;

            },
            close(){
                this.showbox=false;

            },
            add(){
                this.goodsnum++
            },
            reduce(){
                this.goodsnum--;
                if(this.goodsnum<=0){
                    this.goodsnum=0;
                }
            },
            check(value,index){
                this.names=value;
                console.log(this.names)


            },



        }
    }



